.entry.entry_big {
	width: 100%;
	height: 484px;
	float: left;
	margin-bottom: 40px;
	&:hover .entry__text {
		top: auto;
	}
	.img-cover {
		height: 485px;
		width: 100%
	}

	.entry__text {
		top: auto;
		.entry__title {
			font-size: 30px;
			line-height: 1.05
		}
		.entry__introtext {
			opacity: 1;
		}
	}
	&:hover {
		.entry__text {
			top: auto;
		}
	}
	.entry__text {
		top: auto;
	}
}