
.clearfix {
	zoom: 1;
	&:before, &:after {
		content: '0020';
		display: block;
		height: 0;
		overflow: hidden;
	}
	&:after {
		clear: both;
	}
}

.more {
	margin-right: 44px;
	padding-top: 14px;
}

.h2 {
	line-height: 52px !important;
	padding: 6px 73px 0 !important;
	margin-bottom: 20px;
	font-size: 16px;
}

.offer__img {
	img {
		width: auto;
		height: 440px;
	}
}

.house__in__wrap {
	border-radius: 5px;
	box-shadow: -1px 3px 5px 0px #CCC, 2px 2px 5px 0px #CCC;
}

.house__slider__bottles__in, .house__slider__thumbs__in {
	position: relative;
	padding: 0 22px;
	border-left: 1px solid darkgrey;
	border-right: 1px solid darkgrey;
	border-bottom: 1px solid darkgrey;
}

.house__slider__thumbs__in {
	.grayGradient();
	margin-bottom: 58px;
	border-radius: 0 0 5px 5px;
}

.house__slider__thumbs {
	padding: 58px 0 57px 0;
	margin: 0 20px;
}

.house__slider__bottles {
	padding: 21px 0 27px 0;
	margin: 0 50px;
}

.house__slider__thumbs__slide {
	margin: 0 19px 0 21px;
	line-height: 0;
}

.house__slider__bottles__slide {
	padding: 41px 0 39px;
	margin: 0 65px;
}

.house__slider__bottles__slides {
	margin: 0 25px;
}

.house__slider__thumbs__slides {
	margin: 0 80px;
}

.house__slider__bottles__prev, .house__slider__thumbs__prev {
	position: absolute;
	top: 50%;
	left: 70px;
	width: 20px;
	height: 37px;
	margin-top: -15px;
	background: url("@{path-img}icons-s51a623a2b8.png") 0 -713px no-repeat;
	cursor: pointer;
}

.house__slider__bottles__next, .house__slider__thumbs__next {
	position: absolute;
	top: 50%;
	right: 70px;
	width: 21px;
	height: 37px;
	margin-top: -15px;
	background: url("@{path-img}icons-s51a623a2b8.png") 0 -478px no-repeat;
	cursor: pointer;
}

.house__slider__thumbs__next, .house__slider__thumbs__prev {
	margin-top: -18px;
}

.house__expander__in {
	.clearfix();
	.grayGradient();
	border: 1px solid darkgrey;
	border-radius: 5px 5px 0 0;
	.house__expander__button {
		margin-bottom: 62px;
		padding-top: 2px;
		span {
			font-size: 14px;
			color: #B2B2B2;
		}
	}
	&.expanded {
		.house__expander__button {
			margin-top: 57px;
			margin-bottom: 48px;
