		}
	}
}

.house__expander__description__in {
	.clearfix();
	img {
		float: right;
		margin-top: 34px;
		margin-right: 86px;
	}
}

.house__expander__details {
	width: 822px;
	margin: 0 110px 0 143px;
	color: @text-color;
	.house__expander__details__link {
		color: #F56541;
	}
	blockquote {
		padding: 24px 0;
		margin-bottom: 47px;
		text-align: center;
		border-top: 4px dotted #F66338;
		border-bottom: 4px dotted #F66338;
		font-size: 24px;
		span {
			display: block;
			width: 58%;
			margin: 0 auto;
			margin-bottom: -3px;
			line-height: 29px;
		}
	}
	h2 {
		margin-bottom: 13px;
		line-height: 44px;
		font-size: 36px;
		font-family: "MyriadPro-Light", sans-serif;
		font-weight: 300;
		color: #B2004D;
		& + ul {
			padding-top: 12px;
		}
	}

	h3 {
		margin-bottom: 9px;
		font-size: 24px;
		font-weight: normal;
		&:nth-of-type(2) {
			margin-bottom: 8px;
		}
	}

	ul {
		margin-bottom: 17px;
		padding-left: 2px;
		&:nth-of-type(2) {
			margin-bottom: 47px;
		}
		&:nth-of-type(3) {
			li {
				margin-bottom: 12px;
				line-height: 20px;
			}
		}
	}

	li {
		margin-left: -2px;
		margin-bottom: 13px;
		font-size: 16px;
		list-style-type: disc;
		list-style-position: inside;
		line-height: 19px;
	}

	p {
		margin-bottom: 20px;
		line-height: 19px;
		font-size: 16px;
		&:last-of-type {
			margin-bottom: 63px;
		}
	}
}

