.tooltip__in {
	position: relative;
	height: 320px;
	img {
		max-height: 100%;
	}
	.tooltip {
		display: none;
		position: absolute;
		left: 44px;
		top: -27px;
		z-index: 1000;
		padding: 2px 12px;
		font-style: normal;
		color: @white;
		background-color: #333;
	}

	&:hover {
		display: block;
		.tooltip {
			display: block;
		}
	}
}

.icon-wine__red {
