/*common*/
.clearfix {
	zoom: 1;
	&:before, &:after {
		content: "";
		display: table;
	}
	&:after {
		clear: both;
	}
}

.h2 {
	height: auto;
	line-height: 46px;
	padding-top: 4px;
}

/*/common*/

/*top*/

.header {
	height: @topbar;
	/*.header__logo {
		left: 0; *//*by default 90px*//*
	}*/
	.header__nav {
		li {
			a {
				height: @topbar - 3;
				line-height: @topbar - 3;
				font-size: 18px;
				padding-top: 3px;
			}
		}
	}
	select.header__nav {
		top: 22px;
	}
	.header__right {
		top: 20px;
		right: 20px;
		.login {
			height: auto;
			line-height: 20px;
			padding: 2px 20px 0;
		}
	}
}

.is-nottop {
	.header {
		.header__logo {
			height: @topbar;
			width: @topbar;
			i {
				top: 10px;
				left: 6px;
			}
		}
		.header__nav {
			left: 296px;
		}
	}
}

.top {
	.search {
		width: 211px;
		border-radius: @radius-mid;
		display: none;
		z-index: 20;
		&.var-2 {
			border-radius: 0;
			.search__switcher {
				border-radius: 0;
				.search__switcher1 {
					border-top-left-radius: 0;
				}
				.search__switcher2 {
					border-top-right-radius: 0;
				}
			}
			.search__options {
				font-size: 15px;
			}
		}
		&.var-3 {
			border-radius: @radius-mid;
			.search__switcher {
				border-radius: @radius-mid - 1 @radius-mid - 1 0 0;
				.search__switcher1 {
					border-top-left-radius: @radius-mid;
				}
				.search__switcher2 {
					border-top-right-radius: @radius-mid;
				}
			}
		}
		.search__switcher {
			height: @search-switcher-height;
			border-radius: @radius-mid - 1 @radius-mid - 1 0 0;
			a {
				font-size: 17px;
				.font__MyriadPro-Light;
			}
			.search__switcher1 {
				line-height: @search-switcher-height+3;
				height: @search-switcher-height !important;
				border-top-left-radius: @radius-mid - 1;
			}
			.search__switcher2 {
				padding-top: 13px;
				height: @search-switcher-height - 11px !important;
				border-top-right-radius: @radius-mid - 1;
			}
			&:before {
				display: none;
			}
		}
		.search__filters {
			.search__filter {
				padding: 4px 24px;
				width: 100%;
				margin: 0;
				box-sizing: border-box;
				border-bottom: 1px dashed #CCC;
				input {
					margin-left: 1px;
				}
				&:nth-of-type(3) {
					padding-bottom: @air/2;
					.search__showall {
						display: none;
					}
					.search__categ {
						text-align: center;
						li {
							width: 20%;
							position: relative;
							a {
								span {
									display: none;
									position: absolute;
									background: fadeout(@tooltip-bg, 50%);
									color: @tooltip-text;
									bottom: 22px;
									left: 50%;
									margin-left: -35px;
									padding: 0;
									width: 70px;
									border-radius: @radius-mid;
									&:before {
										content: '';
										position: absolute;
										bottom: -@air/3;
										left: 50%;
										margin-left: -@air/3;
										width: 0;
										height: 0;
										border-left: @air/3 solid transparent;
										border-right: @air/3 solid transparent;
										border-top: @air/3 solid fadeout(@tooltip-bg, 50%);
									}
								}
							}
							&:hover {
								label {
									span {
										display: block;
									}
								}
							}
							&:last-of-type {
								margin-right: 0;
							}
						}
					}
				}
			}
		}
	}
}

.superslider {
	overflow: hidden;
	/*height: @superslider-height;*/
	.superslider__slide {
		height: 100%;
		display: none;
		background-position: 50% 50%;
		/*&:nth-of-type(4) {
			background-position: bottom;
		}
		&:nth-of-type(6) {
			background-position: bottom;
		}
		&:nth-of-type(9) {
			background-position: bottom;
		}
		&:nth-of-type(7) {
			background-position: 50%;
		}*/
		.superslider__text {
			.font__RobotoCondensed-Light;
			bottom: 60px;
			span {
				font-family: mr, "MyriadPro-Regular", Arial, sans-serif;
			}
		}
	}
	.superslider__in {
		width: 100%;
		left: 0;
		bottom: 0;
		margin: 0;
	}
	.superslider__next {
		right: 30px;
	}
	.superslider__playpause {
		right: 30px;
	}
	.superslider__pagi {
		right: 30px+20px;
	}
}


/*/top*/

/*page*/
.page {
	padding: 0 250px 0 0;
	&.pr0 {
		padding: 0;
		.search.search_wide {
			display: block;
		}
	}

	.search_wide.search {
		border-radius: 8px;
		.search__switcher {
			.clearfix();
			height: auto;
			border-radius: 8px 8px 0 0;
			overflow: hidden;
			&:before {
				display: none;
			}
			li {
				width: 50%;
				float: left;
				display: table;
				height: 88px;
				.search__switcher1,
				.search__switcher2 {
					font-family: 'MyriadPro-Light', Sans-Serif;
					font-size: 22px;
					position: static;
					float: none;
					display: table-cell;
					vertical-align: middle;
					line-height: 1.2;
					width: 100%;
					border-radius: 0;
					padding-top: 0;
				}
			}
		}
		.search__filters {
			.search__filter:nth-of-type(3) {
				.search__categ li {
					width: 50%;
					margin-right: 0;
				}
			}
		}
	}

	.search.search_wide {
		* {
			box-sizing: border-box;
		}
		position: static;
		width: auto;
		box-shadow: 0 10px 15px rgba(0, 0, 0, 0.4);
		display: none;
		padding: 25px 0 35px;
		margin-bottom: 58px;
		border-radius: 0px;
		.search__input {
			padding: 0px 72px 0 69px;
			margin-bottom: 23px;
			input {
				padding: 2px 0 0px 21px;
				height: 34px;
				font-size: 16px;
				font-family: mr, sans-serif;
				display: block;
				width: 100%;
				margin-right: -34px;
				float: left;
				line-height: 1;

			}
			.search__inputicon {
				width: 34px;
				position: static;
				background-color: #BCBDC0;
			}
		}
		.search__switcher {
			border-radius: 0;
			li {
				height: 101px;
				.search__switcher1,
				.search__switcher2 {
					padding-top: 8px;
					font-size: 26px;
				}
			}
		}
		.search__filters {
			padding: 0 3.5% 0 5.66%;
		}
		.search__filter {
			margin-top: 32px;
			padding-top: 9px;
			padding-left: 0;
			margin-right: 2.16%;
			float: left;
			&.search__filter1,
			&.search__filter2,
			&.search__filter4,
			&.search__filter5 {
				//        width: 188px;
				width: 14.88%;
				padding-right: 0.5%;
				margin-left: 0;

			}
			&.search__filter5 {
				margin-right: 0;
			}
			&.search__filter3 {
				width: 31.84%;
			}

			.search__title {
				font-size: 21px;
				margin-bottom: 6px;
			}
			.search__options {
				font-size: 16px;
				line-height: 1.2;

				span {
					overflow: hidden;
					display: block;
				}

				input {
					float: left;
					margin-left: 0;
					margin-right: 12px;
					margin-top: 4px;
					clear: left;
				}
			}
		}
		.search__message {
			display: none;
		}
		.search__reset,
		.search__pdf {
			text-transform: uppercase;
			&.search__reset {
				padding-top: 3px;
				padding-right: 18px;
				margin-bottom: 20px;
			}
			&.search__pdf {
				padding-right: 18px;
				i {
					margin-top: -3px;
				}
				span {
					padding-bottom: 0;
					line-height: 1.3;
				}
			}
		}
		.catslider {
			position: relative;
			margin-top: 27px;
			.catslider__in {
				.search__categ {
					margin: 0 0 0 38px;
					.cycle-slide {
						text-align: center;
						margin-right: 16px;
						.cat_item {
							i {
								margin-bottom: 9px;
							}
							span {
								.font__MyriadPro-Light;
								text-transform: lowercase;
								font-size: 16px;
								padding-top: 0;
							}
						}
					}
				}
			}
			.catslider__prev {
				background: url("@{path-img}icons-s51a623a2b8.png") 0 -713px no-repeat;
				width: 20px;
				height: 37px;
				position: absolute;
				top: 50%;
				margin-top: -18.5px;
				left: 0;
				cursor: pointer;
			}
			.catslider__next {
				background: url("@{path-img}icons-s51a623a2b8.png") 0 -478px no-repeat;
				width: 21px;
				height: 37px;
				position: absolute;
				right: 29px;
				cursor: pointer;
				top: 50%;
				margin-top: -18.5px;
			}
		}
	}
}

.home-logos__list {
	margin-top: 56px;
}

